@font-face {
    font-family: 'ignsearchlocator';
    src: url(data:application/vnd.ms-fontobject;base64,);
    src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'),
        url(data:font/woff2;base64,) format('woff2'),
        url(data:font/woff;base64,) format('woff'),
        url(data:font/ttf;base64,) format('truetype'),
        url(data:image/svg+xml;base64,#ignsearchlocator) format('svg');
    font-weight: normal;
    font-style: normal;
}

.m-panel.ign-searchlocator-panel [class^="icon-"]:before,
.m-panel.ign-searchlocator-panel [class*=" icon-"]:before {
    font-family: "ignsearchlocator";
    font-style: normal;
    font-weight: normal;
    speak: never;
    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
    margin-left: .2em;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.m-panel.ign-searchlocator-panel .icon-help:before { content: '\e800'; }
.m-panel.ign-searchlocator-panel .icon-locate:before { content: '\e801'; }
.m-panel.ign-searchlocator-panel .icon-ignsearch:before { content: '\e807'; }
.m-panel.ign-searchlocator-panel .icon-remove:before { content: '\e93e'; }
.m-panel.ign-searchlocator-panel .icon-coordinates:before { content: '\e94a'; }
.m-panel.ign-searchlocator-panel .icon-info:before { content: '\e94b'; }
.m-panel.ign-searchlocator-panel .icon-position:before { content: '\e95a'; }
.m-panel.ign-searchlocator-panel .icon-catastro:before { content: '\e99f'; }
